.App {
  text-align: center;
  background-image: linear-gradient(to top, #fbc2eb 0%, #a6c1ee 100%);
}

.search-form{
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar{
  width: 50%;
  border: none;
  padding: 10px;
}

.search-button{
background: lightcoral;
border: none;
padding: 10px 20px;
color: white;
}

.recipes{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.catch-phrase{
  font-family: Verdana;
  font-size: 70px;
}

.info{
  font-family: Verdana;
  font-size: 15px;
}

.creds{
  font-family: Verdana;
  font-size: 10px;
  margin:0;
}